/*
 *  Utilities: Responsive classes
 *  ---
 *  Utility classes for responsive breakpoints
 */

// Loop through the responsive breakpoint map and generate classes (e.g: u-smartphone-landscape-only).
@each $breakpoint, $value in $breakpoints {
  .u-#{$breakpoint}-only {
    @include mappy-bp(#{$breakpoint}){
      display: none !important;
    }
  }

  .u-hide-#{$breakpoint} {
    @include mappy-bp(max-width #{$breakpoint}){
      display: none !important;
    }
  }
}