 *  ---
 *  The list-inline object simply displays a list of items in one line.
 */
.o-list-inline {
  margin: 0;
  padding: 0;
  list-style: none; }
  .o-list-inline > li {
    display: inline-block; }

.o-list-inline--spaced > li:not(:last-child) {
  margin-right: 13px; }

/*
 *  Object: Grid
 *  ---
 *  Simple re-usable flexbox grids
 */
.o-grid {
  display: flex;
  flex-wrap: wrap; }

.o-grid > * {
  flex: 0 0 25%; }
  @media all and (max-width: 93.6875em) {
    .o-grid > * {
      flex: 0 0 33.33333%; } }
  @media all and (max-width: 63.9375em) {
    .o-grid > * {
      flex: 0 0 50%; } }
  @media all and (max-width: 47.9375em) {
    .o-grid > * {
      flex: 1 1 100%; } }

.o-grid--third > * {
  flex: 0 0 33.33333%; }
  @media all and (max-width: 47.9375em) {
    .o-grid--third > * {
      flex: 1 1 100%; } }

.o-grid--half > * {
  flex: 0 0 50%; }
  @media all and (max-width: 47.9375em) {
    .o-grid--half > * {
      flex: 1 1 100%; } }

.o-grid--fluid > * {
  flex-grow: 1;
  flex-shrink: 1; }

