/*
 * Components
 * Specific UI components. This is where majority of our work takes place and
 * our UI components are often composed of Objects and Components
 */
/*
 *  Icon animations
 *  ---
 */
.c-icons-animation {
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: absolute; }

.c-icons-animation__icon {
  position: absolute;
  top: 0;
  left: 0;
  will-change: transform; }
  .c-icons-animation__icon img {
    opacity: .25;
    will-change: transform; }

/*
 *  Icon animations
 *  ---
 */
.c-status {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 70vh;
  height: 70vh;
  margin-top: -35vh;
  margin-left: -35vh;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 300;
  transition: color .25s; }

.c-status-title {
  position: relative;
  z-index: 2;
  font-weight: inherit;
  margin: 0;
  text-align: center;
  font-size: 3.61111rem;
  line-height: 1.5;
  color: white;
  pointer-events: none; }
  .c-status-title svg {
    fill: white;
    width: 45px;
    height: 45px;
    margin-top: 0.38889em; }
  .c-status-title > * {
    display: block;
    width: 100%; }
  .c-status-title .inner {
    color: #e7036e;
    font-weight: 700; }

.c-status-message {
  font-size: 3.33333rem;
  line-height: 1;
  font-weight: 200;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40vh;
  margin-left: -20vh;
  text-align: center;
  z-index: 3;
  opacity: 0;
  line-height: 1.4; }
  .c-status-message span {
    background-color: rgba(0, 0, 0, 0.3);
    color: #fff;
    display: inline;
    padding: 0.45rem;
    /* Needs prefixing */
    -webkit-box-decoration-break: clone; }

.c-status-message--error .subtitle {
  font-size: 2.22222rem;
  line-height: 1.5; }

.c-status-message-inner {
  display: inline-flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column; }

.c-status-circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }
  .c-status-circle > * {
    position: absolute;
    border-radius: 50%; }

.c-status-circle-border {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #1b1b1b;
  will-change: transform; }

.c-status-circle-bg {
  top: 5px;
  left: 5px;
  width: calc(100% - 10px);
  height: calc(100% - 10px);
  background-color: #1b1b1b;
  will-change: transform; }

.c-status-circle-gif {
  background-size: cover;
  top: 5px;
  left: 5px;
  width: calc(100% - 10px);
  height: calc(100% - 10px);
  will-change: transform;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0; }

/*
 *  Icon animations
 *  ---
 */
.c-logo {
  position: fixed;
  left: 3vw;
  top: 3vw;
  fill: rgba(255, 255, 255, 0.8); }

/*
 *  Icon animations
 *  ---
 */
.c-employee-avatar {
  position: absolute;
  z-index: 2;
  top: 5vh;
  right: 5vh;
  border-radius: 50%;
  object-fit: cover;
  transform: scale(0);
  opacity: 0;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.3); }
  .c-employee-avatar img {
    width: 10vh;
    height: 10vh;
    object-fit: cover; }

.c-employee-name {
  font-size: 10vw;
  font-weight: 700;
