/*
 *  Badge indication animation/icon
 *  ---
 */
.c-badge-indication {
  position: absolute;
  z-index: 2;
  bottom: 5vh;
  right: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1b1b1b;
  box-shadow: 0 0 6.66667em 120px #1b1b1b;
  padding: 1.44444em; }

.c-badge-indication__ripples {
  position: relative;
  margin-right: 2.55556em; }

/*
 *  Ripples
 *  ---
 */
.c-ripples {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .c-ripples > * {
    position: absolute;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    border: 2px solid white;
    opacity: .1; }
    .c-ripples > *:nth-child(1) {
      width: 20%;
      height: 20%; }
    .c-ripples > *:nth-child(2) {
      width: 40%;
      height: 40%; }
    .c-ripples > *:nth-child(3) {
      width: 60%;
      height: 60%; }
    .c-ripples > *:nth-child(4) {
      width: 80%;
      height: 80%; }
    .c-ripples > *:nth-child(5) {
      width: 100%;
      height: 100%; }

