/*
 *  Default Settings
 *  ---
 *  Only override the !default variables in '_settings.project.scss' to suit your project needs
 */

/*
 * Base typographical styles and baseline grid.
 */
$global-font-size: 18px !default;
$global-line-height: 26px !default;

/**
 * Font family's
 */
$primary-font-family-fallback: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$primary-font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$secondary-font-family-fallback: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$secondary-font-family: $primary-font-family !default;

/**
 * Font weights
 */
$light: 300 !default;
$normal: 400 !default;
$semi-bold: 600 !default;
$bold: 700 !default;
$extra-bold: 800 !default;

/**
 * Font sizes
 */
$font-size-h1: 36px !default;
$font-size-h2: 30px !default;
$font-size-h3: 26px !default;
$font-size-h4: 22px !default;
$font-size-h5: 18px !default;
$font-size-h6: 16px !default;
$font-size-tiny: 12px !default;
$font-size-x-small: 14px !default;
$font-size-small: 16px !default;
$font-size-default: $global-font-size !default;
$font-size-medium: 22px !default;
$font-size-between: 26px !default;
$font-size-large: 30px !default;
$font-size-huge: 60px !default;

/**
 * Debug mode
 */
$debug-mode: false !default;

/*
 * Spacing values are determined based on your project’s global line height (i.e
 * your baseline grid). It is not recommended that you modify these following
 * variables (it can break your vertical rhythm), but if you need to, you can.
 */
$global-spacing-unit: round($global-line-height) !default;

/**
 * Wrapper
 */
$wrapper-width: 1600px !default;
$wrapper-gutter: $global-spacing-unit !default;

/*
 * How many times larger/smaller than the default should our spacing unit
 * variants be?
*/
$global-spacing-unit-factor-tiny: 0.25 !default;
$global-spacing-unit-factor-small: 0.5 !default;
$global-spacing-unit-factor-medium: 1.75 !default;
$global-spacing-unit-factor-large: 2.5 !default;
$global-spacing-unit-factor-huge: 4 !default;

/**
 * z-index levels
 */
$z-indexes: (
        alpha : 1,
        beta : 2,
        gamma : 3,
        delta : 4,
        epsilon : 5
) !default;

/**
 * Breakpoints
 */
$breakpoints: (
        smartphone: 375px,
        smartphone-landscape: 667px,
        tablet: 768px,
        tablet-landscape: 1024px,
        desktop: 1200px,
        desktop-wide: 1500px
);

/**
 * Grid columns
 *
 * Set the number of columns and specify the width of the gutters.
 */
$grid-columns: 12 !default;
$grid-gutter-width: 0 !default;